.footer {
    background-color: #4c4631;
    color: white;
    padding: 2rem 1rem;
  
    .container {
      max-width: 1200px;
      margin: 0 auto;
    }
  
    .footer-grid {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 2rem;
    }
  
    .footer-section {
      flex: 1 1 200px;
  
      h4 {
        font-weight: bold;
        margin-bottom: 1rem;
        color: white;
        font-size: 1.125rem;
      }
  
      ul {
        list-style: none;
        padding: 0;
  
        li {
          margin-bottom: 0.5rem;
        }
      }
  
      p {
        line-height: 1.5;
        font-size:  0.875rem;
      }
  
      a {
        text-decoration: none;
      }
    }
  
    .social-icons {
      display: flex;
      gap: 1rem;
  
      svg {
        transition: transform 0.3s;
        cursor: pointer;
  
        &:hover {
          transform: scale(1.2);
        }
      }
    }
  
    hr {
      margin: 2rem 0 1rem;
      border: none;
      border-top: 1px solid #ffffffa1;
    }
  
    .footer-copy {
      text-align: center;
      font-size: 0.9rem;
    }
  }
  