.btn {
  padding: 0.4rem 1rem;
  font-size: 0.875rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  font-family: 'Inter', sans-serif;
  font-weight: 500;
}

.btn-dark {
  background-color: #484230;
  color: #fff;
}

.btn-light {
  background-color: #e0e0e0;
  color: #000;
}